*{
	margin:0;
	padding:0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
*::after{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
*::before{
  	-webkit-box-sizing: border-box;
  	        box-sizing: border-box;
}
ul,ol,dl{
  	list-style:none;
}
a{
  	color: inherit;
}


